/*
 WISE Project Styles
 */
body {
  background: #eeeeee;
}
body.vle {
  overflow: hidden;
}

a:hover, a:focus {
  color: #1C8CA8;
}

blockquote {
  background-color: #e7f7fb;
  padding: 8px;
  margin: 16px 0;
  border-style: solid;
  border-color: #1C8CA8;
  border-width: 0 0 0 3px;
}

.has-indicator:after {
  content: "";
  position: absolute;
  border-radius: 50%;
  padding: 5px;
  background-color: #F05843;
}

.has-indicator--icon-button:after {
  top: 25px;
  left: 5px;
}

.badge {
  border-radius: 4px;
  padding: 2px 6px;
  font-size: 12px;
  font-weight: 500;
  font-style: normal;
  background-color: #aaaaaa;
}
.badge.md-button {
  min-width: 0;
  min-height: 0;
  line-height: inherit;
}
.badge.md-button:hover, .badge.md-button:focus {
  background-color: #aaaaaa;
}
.badge.md-button:focus {
  outline: 1px dotted #aaaaaa;
}

.badge--info {
  background-color: #ef6c00;
  color: #ffffff;
}

.badge--warn {
  background-color: #c62828;
  color: #ffffff;
}

.badge--success {
  background-color: #00C853;
  color: #ffffff;
}

.divider--withmargin {
  margin: 16px 0;
}

.divider--dashed {
  border-top-style: dashed;
}

a {
  color: #1C8CA8;
  cursor: pointer;
}

.active {
  background-color: rgba(158, 158, 158, 0.2);
  color: rgba(0, 0, 0, 0.87);
}

.avatar {
  border-radius: 50%;
  box-sizing: content-box;
}

.avatar--square {
  border-radius: 4px;
}

.avatar.md-18 {
  height: 30px;
  width: 30px;
}
.avatar.md-24 {
  height: 36px;
  width: 36px;
}
.avatar.md-36 {
  height: 48px;
  width: 48px;
}
.avatar.md-48 {
  height: 60px;
  width: 60px;
}

.avatar--icon {
  background-color: #dddddd;
  white-space: normal !important;
}
.avatar--icon:not(.md-avatar) {
  padding: 6px;
}
.avatar--icon.md-18 {
  height: 18px;
  width: 18px;
}
.avatar--icon.md-24 {
  height: 24px;
  width: 24px;
}
.avatar--icon.md-36 {
  height: 36px;
  width: 36px;
}
.avatar--icon.md-48 {
  height: 48px;
  width: 48px;
}

md-toolbar.md-light-theme:not(.md-menu-toolbar) md-icon {
  color: rgba(0, 0, 0, 0.54);
}
md-toolbar.md-light-theme:not(.md-menu-toolbar) .md-button:disabled md-icon {
  color: rgba(0, 0, 0, 0.26);
}

md-icon.primary, .md-button:not([disabled]).primary {
  color: #1C8CA8 !important;
}
md-icon.success, .md-button:not([disabled]).success {
  color: #00C853 !important;
}
md-icon.warn, .md-button:not([disabled]).warn {
  color: #c62828 !important;
}
md-icon.info, .md-button:not([disabled]).info {
  color: #ef6c00 !important;
}
md-icon.accent, .md-button:not([disabled]).accent {
  color: #F05843 !important;
}
md-icon.accent-1, .md-button:not([disabled]).accent-1 {
  color: #795C3A !important;
}
md-icon.accent-2, .md-button:not([disabled]).accent-2 {
  color: #CAD266 !important;
}

md-input-container.md-wise-theme label {
  color: rgba(0, 0, 0, 0.87);
}

md-select-menu.md-default-theme md-option[selected], md-select-menu md-option[selected] {
  background-color: #f4fbfd;
}

.md-autocomplete-suggestions-container.md-default-theme li .highlight,
.md-autocomplete-suggestions-container li .highlight {
  color: #1C8CA8;
  background-color: #f4fbfd;
}

