.l-constrained {
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  position: relative;
}
@media (min-width: 600px) {
  .l-constrained {
    width: 1280px;
  }
}
@media (min-width: 1920px) {
  .l-constrained {
    width: 1920px;
  }
}

.l-constrained-md {
  width: 960px;
  max-width: 100%;
}

.l-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: #ffffff;
  border-top: 1px solid #eeeeee;
}

.button--footer {
  margin: 0;
  padding-top: 0;
  padding-bottom: 0;
  min-width: 0;
  display: flex;
}

.button--footer__element {
  padding-left: 8px;
}

.l-header {
  z-index: 3;
}
.l-header .logo {
  margin-left: 0 !important;
  height: 36px;
  width: 36px;
  vertical-align: middle;
}
.l-header .logo-link {
  min-width: auto;
  display: none;
  padding: 0 4px;
  margin-right: 12px;
}
@media only screen and (min-width: 600px) {
  .l-header .logo-link {
    display: block;
  }
}
.l-header .logo-link:hover, .l-header .logo-link:focus {
  border: 0 none;
}
@media only screen and (max-width: 599px) {
  .l-header .md-toolbar-tools h1, .l-header .md-toolbar-tools h2, .l-header .md-toolbar-tools h3 {
    font-size: 15px;
  }
}

.l-main {
  background-color: #eeeeee;
}

.l-main--with-toolbar {
  margin-top: 42px;
}

#content {
  transition: margin-top 0.5s;
}

.view-content {
  margin: 0 auto;
  padding: 8px;
  position: absolute;
  left: 0;
  right: 0;
  transition: opacity 500ms;
}
@media only screen and (min-width: 960px) {
  .view-content {
    padding: 16px;
  }
}
.view-content.ng-enter {
  opacity: 0;
}
.view-content .ng-enter-active {
  opacity: 1;
  transition-delay: 250ms;
}
.view-content.ng-leave-active, .view-content.ng-hide {
  opacity: 0;
}
.view-content.ng-hide-add, .view-content.ng-hide-add-active, .view-content.ng-hide-remove, .view-content.ng-hide-remove-active {
  opacity: 0;
}

.view-content--with-sidemenu {
  padding: 8px;
}
@media only screen and (min-width: 600px) {
  .view-content--with-sidemenu {
    margin-left: 54px;
    padding: 16px;
  }
}

@media only screen and (min-width: 600px) {
  [dir=rtl] .view-content--with-sidemenu {
    margin-left: auto;
    margin-right: 54px;
  }
}

.content-head {
  margin: 8px 0;
}
.content-head h1,
.content-head h2,
.content-head h3 {
  font-weight: 300;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 36px;
}
