.outside-content__source {
  margin-top: 4px;
  text-align: end;
}
.outside-content__source a {
  max-width: 240px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
}

.notebook-toolbar md-divider {
  margin: 8px 0;
}
@media only screen and (max-width: 959px) {
  .notebook-toolbar {
    border-top: 1px solid #dddddd;
  }
}

.notebook-toolbar__add-menu {
  position: absolute;
  bottom: 40px;
}
.notebook-toolbar__add-menu .md-fab-action-item {
  background-color: #ffffff;
}

.notebook-toolbar__add-icon {
  border-radius: 50%;
}

#closeNotebookSettingsButton {
  float: right;
}

[dir=rtl] #closeNotebookSettingsButton {
  float: left;
}

highchart {
  display: block;
}