// 2. Base
.l-constrained {
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  position: relative;

  @media (min-width: $layout-breakpoint-xs) {
      width: $layout-breakpoint-md;
  }

  @media (min-width: $layout-breakpoint-lg) {
      width: $layout-breakpoint-lg;
  }
}

.l-constrained-md {
  width: $layout-breakpoint-sm;
  max-width: 100%;
}
